import React from 'react';
import './TransfersBlock.scss';

import topup from '../../img/transfers/topup.svg';
import send from '../../img/transfers/send.svg';
import withdraw from '../../img/transfers/withdraw.svg';
import { Link } from 'react-router-dom';
import { TRANSFERS_TOPUP_ROUTE, TRANSFERS_ROUTE, TRANSFERS_SEND_ROUTE, TRANSFERS_WITHDRAW_ROUTE} from './../../utils/consts';

const Transfers = React.memo(({classes}) => {
  return (
    <div className={`transfers-block ${classes}`}>
      <h1 className='transfers-block__title'>Transfers and Payments</h1>
      <div className='transfers-block__options'>
        <Link to={TRANSFERS_ROUTE + '/' + TRANSFERS_TOPUP_ROUTE} className='transfers-block__option'>
          <img src={topup} alt='TOP UP' />
          <p className='option__title'>Top up balance</p>
        </Link>
        <Link to={TRANSFERS_ROUTE + '/' + TRANSFERS_SEND_ROUTE} className='transfers-block__option'>
          <img src={send} alt='SEND' />
          <p className='option__title'>Send money</p>
        </Link>
        <Link to={TRANSFERS_ROUTE + '/' + TRANSFERS_WITHDRAW_ROUTE} className='transfers-block__option'>
          <img src={withdraw} alt='Withdraw' />
          <p className='option__title'>Withdraw</p>
        </Link>
      </div>
    </div>
  );
});

export default Transfers;