import React from 'react';
import './AccountDetails.scss';
import { Link } from 'react-router-dom';
import { ACCOUNT_PROFILE_ROUTE, ACCOUNT_ROUTE } from './../../utils/consts';
import { observer } from 'mobx-react-lite';
import userStore from '../../store/UserStore';

const AccountDetails = observer(({classes, details}) => {
  return (
    <div className={`account-details ${classes}`}>
      <Link to={ACCOUNT_ROUTE + '/' + ACCOUNT_PROFILE_ROUTE} className='account-details__title'>Account details</Link>
      <div className='account-details__content'>
        <div className='balance'>
          <h1 className='balance__title'>Total Balance</h1>
          <div className='balance__money'>
            <div className='balance__num'>{details.balance}</div>
            <p className='balance__currency'>USD</p>
          </div>
        </div>
        <div className='month-limit'>
          <p className='month-limit__title'>Online payments limit for the month</p>
          <progress className='month-limit__progress' max={userStore.limit} value={userStore.limit - userStore.sendedMoney}></progress>
          <p className='month-limit__left'>{userStore.sendedMoney} out of {userStore.limit} left</p>
        </div>
      </div>
    </div>
  );
});

export default AccountDetails;