import React, { useRef, useEffect, useState } from 'react';
import filterByCategory  from '../../img/filter/category.svg';
import filterByDate  from '../../img/filter/date.svg';
import { observer } from 'mobx-react-lite';
import userStore from '../../store/UserStore';
import { getDateName, getMonthBeforeDate } from './../../utils/dates';
import { getAllTypesSum, getSum } from './../../utils/transactions';
import Chart from '../Chart/Chart';
import { getMoneyString } from './../../utils/money';
import './ChartOverview.scss';
import SortByCategory from './../FilterModals/SortByCategory/SortByCategory';
import SortByDateModal from './../FilterModals/SortByDate/SortByDateModal';
import FilterStore from '../../store/FilterStore';
import DateStore from '../../store/DateStore';

const ChartOverview = observer(({classes, dimensions}) => {
  const [data, setData] = useState([]);
  const [startDate, setStartDate] = useState();
  const [endDate, setEndDate] = useState();
  const [sumInRangeDates, setSumInRangeDates] = useState();

  const [showCategory, setShowCategory] = useState(false);
  const [showDate, setShowDate] = useState(false);


  const filterHandler = (e) => {
    console.log(e.currentTarget.id);
    setShowCategory(false);
    setShowDate(false);
    if(e.currentTarget.id === 'category') showCategory ? setShowCategory(false) : setShowCategory(true);
    if(e.currentTarget.id === 'date') showDate ? setShowDate(false) : setShowDate(true);
  }

  // useEffect(() => {
  //   if(userStore.summary !== null) {
  //     let data = [];
  //     for (const [key, count] of Object.entries(userStore.summary)) 
  //       data.push({type: key, value: count});
  //     setData(data);
  //     setSumInRangeDates(getSum(data));
  //   } else {
  //     setSumInRangeDates(0)
  //   }
  //   const today = new Date().toLocaleDateString('en-ca');
  //   const dateOfFirstMadedTransaction = userStore.transactions ? userStore.transactions.date : null;
  //   setEndDate(getDateName(today));
  //   setStartDate(getDateName(dateOfFirstMadedTransaction))
  // }, [userStore.summary])

  useEffect(() => {
    if(FilterStore.filteredByDate) {
      if(DateStore.firstDate < DateStore.secondDate) {
        setStartDate(getDateName(DateStore.firstDate))
        setEndDate(getDateName(DateStore.secondDate));
      }
      else {
        setStartDate(getDateName(DateStore.secondDate))
        setEndDate(getDateName(DateStore.firstDate));
      }
    } else {
      const today = new Date().toLocaleDateString('en-ca');
      const dateOfFirstMadedTransaction = userStore.transactions ? userStore?.transactions[0]?.date : null;
      setEndDate(getDateName(today));
      setStartDate(getDateName(dateOfFirstMadedTransaction))
    }
    if(FilterStore.transactions) {
      const data = getAllTypesSum(FilterStore.transactions);
      setSumInRangeDates(getSum(data));
      setData(data);
    }
  }, [FilterStore.filteredByDate, FilterStore.transactions]);

  return (
    <div className={`chartOverview ${classes}`}>
      <div className='chartOverview__navbar'>
        <h1 className='chartOverview__title'>Chart</h1>
        <div className='chartOverview__filters'>
          <div className='chartOverview__filter'>
            <button id='category' className='filter__icon' onClick={filterHandler}>
              <img src={filterByCategory} alt="Filter By Category" />
            </button>
            <SortByCategory isShow={showCategory} setShow={setShowCategory} />
          </div>
          <div className='chartOverview__filter'>
            <button id='date' className='filter__icon' onClick={filterHandler}>
              <img src={filterByDate} alt="Filter By Date" />
            </button>
            <SortByDateModal isShow={showDate} setShow={setShowDate}  classes='chartOverview__dateFilter'/>
          </div>       
        </div>
      </div>
      <div className='chartOverview__content'>
        <Chart dimensions={dimensions} data={data} />
        <div className='chartOverview__info'>
          <div className='chartOverview__date'>{startDate} - {endDate}</div>
          <div className='chartOverview__money'>{getMoneyString(sumInRangeDates)}</div>
          <div className='chartOverview__percent'> {(userStore.sendedMoney / userStore.limit * 100).toFixed(2)}%</div>
        </div>
      </div>
    </div>
  );
});

export default ChartOverview;