import React, { useEffect } from 'react';
import './Overview.scss';

import AccountDetails from './../../components/AccountDetails/AccountDetails';
import Promo from './../../components/Promo/Promo';
import TransfersBlock from '../../components/TransfersBlock/TransfersBlock';
import Transactions from './../../components/Transactions/Transactions';
import userStore from '../../store/UserStore';
import { observer } from 'mobx-react-lite';
import Spinner from './../../components/Spinner/Spinner';
import SearchInput from './../../components/SearchInput/SearchInput';
import { Link } from 'react-router-dom';
import { ARCHIVE_ROUTE } from '../../utils/consts';
import { useDebounce } from './../../hooks/useDebounce';
import { useState } from 'react';
import { useSort } from '../../hooks/useSort';
import ChartOverview from './../../components/ChartOverview/ChartOverview';
import { ARCHIVE_TRANSACTIONS_ROUTE } from './../../utils/consts';

const Overview = observer(() => {
  const [transactions, setTransactions] = useState(null);
  const [transactionsMain, setTransactionsMain] = useState([]);
  const [sorting, setSorting] = useState(false);
  const [isResults, setIsResults] = useState(true);
  useEffect(() => {
    let fetchedTransactions
    if(userStore?.transactions?.length > 15)
      fetchedTransactions = userStore?.transactions?.slice(userStore.transactions.length - 15);
    else 
      fetchedTransactions = userStore?.transactions;
    setTransactions(fetchedTransactions);
    setTransactionsMain(fetchedTransactions);
  }, [userStore.transactions]);

  const sortSearch = useSort((value) => {
    const transactions = transactionsMain.filter((transaction) => transaction.value.startsWith(value));
    setTransactions(transactions);
    if(transactions.length === 0) setIsResults(false);
    else setIsResults(true);
  }, setSorting);

  const sortSearchDebounce = useDebounce(sortSearch, 500);

  return (
    <div className='overview'>
      <AccountDetails classes='overview__item overview__account' details={{'balance': userStore.balance}}/>
      <Promo classes='overview__item overview__promo' 
        isSlider={true}
        title='Get exclusive discounts for any payment method'
        subtitle='by making your first purchase'/>
      <TransfersBlock classes='overview__item overview__transfers border-gradient'/>
      <div className='overview__transactions overview__item  border-gradient'>
        <div className='transactions__navbar'>
          <h1 className='transactions__title'>Latest transactions</h1>
          <SearchInput classesContainer='transactions__search' onChange={(value) => {
            sortSearchDebounce(value)
          }}/>
          <Link to={ARCHIVE_ROUTE + '/' + ARCHIVE_TRANSACTIONS_ROUTE} className='transactions__more'>See all</Link>
        </div>
        {userStore.isFetchingTransactions || sorting
          ? 
            <div className='overview__item overview__transactions'><Spinner /></div>
          : 
            <Transactions  isResults={isResults} transactions={transactions}/>
        }
      </div>
      <ChartOverview 
        classes='overview__item overview__chart border-gradient' 
        dimensions={{
          width: 270,
          height: 270,
          radius: 80
        }}/>
    </div>
  );
});

export default Overview;